(function () { // REMOVE IF DRUPAL.

  'use strict';

  // Use context instead of document IF DRUPAL.
  var search_input = document.getElementById('serach-input');
  var search_wrapper = document.getElementById('search-wrapper');
  var mo_search_icon = document.getElementById('mobile-serach-icon-block');

  //search-button

  //search-button


  // Mobile Menu Show/Hide.
  search_input.addEventListener('click', function (e) {
    search_wrapper.classList.toggle('search-active');
  });

  mo_search_icon.addEventListener('click', function (e) {
    search_wrapper.classList.toggle('mobile-search-active');
  });

})(); // REMOVE IF DRUPAL.

